@import url(https://fonts.googleapis.com/css?family=Overpass:400,600&display=swap);
html,
body,
#root {
  width: 100%;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  font-size: 14px;
  font-family: 'Overpass', sans-serif;
  background: linear-gradient(-225deg, #5271c4 0%, #b19fff 48%, #eca1fe 100%);
  background-size: cover; }

* {
  box-sizing: border-box; }

main {
  width: 100%;
  max-width: 500px;
  margin: 0 auto;
  padding: 20px; }

.logo {
  display: flex;
  align-items: center;
  margin: 0 0 20px;
  color: #fffff2;
  font-weight: normal; }
  .logo > img {
    margin: 0 10px 0 0; }

footer {
  text-align: center;
  color: #fffff2;
  opacity: 0.75;
  font-size: 0.8rem; }
  footer a {
    color: inherit;
    text-decoration: none;
    border-bottom: 1px dotted #fffff2; }

.controls {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: auto;
  display: flex;
  align-items: center;
  padding: 20px 15px 20px;
  background: #222;
  border-radius: 0 0 3px 3px;
  z-index: 999;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px); }
  .controls__clock {
    flex-shrink: 0;
    background: #fefefe;
    border-radius: 50%;
    margin: 0 10px 0 0; }
    .controls__clock .react-clock__face {
      padding: 5px;
      border: 0; }
    .controls__clock .react-clock__mark__body {
      margin-top: 2px;
      background: #40476d; }
    .controls__clock .react-clock__hour-hand__body, .controls__clock .react-clock__minute-hand__body {
      background: #40476d; }
  .controls__group {
    flex-shrink: 1;
    width: 100%; }
  .controls__buttons {
    display: flex;
    margin: 0; }
    .controls__buttons > button {
      width: 100%;
      height: 100%;
      margin: 0 1px 0 0;
      padding: 10px 20px;
      color: #fffff2;
      background: #5271c4;
      border: 0;
      border-radius: 0;
      outline: 0;
      vertical-align: middle;
      cursor: pointer;
      -webkit-user-select: none;
         -moz-user-select: none;
          -ms-user-select: none;
              user-select: none;
      -webkit-transition: all 0.5s ease;
      transition: all 0.5s ease; }
      .controls__buttons > button:hover {
        background: #3a58a9; }
      .controls__buttons > button:active {
        background: #2d4483; }
      .controls__buttons > button:first-of-type {
        border-radius: 3px 0 0 0; }
      .controls__buttons > button:last-of-type {
        border-radius: 0 3px 0 0;
        margin: 0; }
  .controls__input {
    display: flex;
    align-items: center;
    padding: 5px 15px;
    background: #fffff2;
    color: #40476d;
    border-radius: 0 0 3px 3px;
    outline: 0;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease; }
    .controls__input > input {
      flex-shrink: 1;
      width: 100%;
      border: 0;
      outline: 0; }
    .controls__input > label {
      flex-shrink: 0;
      cursor: pointer; }
      .controls__input > label > .is--unchecked {
        -webkit-filter: grayscale(100%);
                filter: grayscale(100%); }

.post {
  display: block;
  margin: 20px 0;
  padding: 10px;
  background: #fffff2;
  border: 0;
  border-radius: 3px; }
  .post__file {
    display: flex;
    align-items: center;
    margin: -10px -10px 10px;
    padding: 5px 10px;
    color: #40476d;
    font-family: monospace;
    font-size: 0.8rem;
    line-height: normal;
    background: #e5e5e5;
    border-radius: 3px 3px 0 0; }
    .post__file > a {
      color: #40476d;
      text-decoration: none;
      border-bottom: 1px dotted #40476d;
      opacity: 0.5;
      -webkit-transform: all 0.5s ease;
              transform: all 0.5s ease; }
      .post__file > a:hover {
        opacity: 1; }
    .post__file > span {
      margin: 0 5px 0 0; }
  .post__content {
    opacity: 0.35;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease; }
    .post__content:hover {
      opacity: 1; }
  .post__profile-info {
    margin: 0 0 5px; }
    .post__profile-info > a {
      color: #b19fff;
      font-weight: bold;
      text-decoration: none; }
      .post__profile-info > a:hover {
        text-decoration: underline; }
      .post__profile-info > a > img {
        width: 25px;
        margin: 0 5px 0 0;
        border-radius: 3px;
        vertical-align: middle; }
  .post__photo {
    width: 100%;
    height: 0;
    padding-bottom: 100%;
    background: #fefefe no-repeat center/cover;
    border-radius: 3px; }
    .post__photo-description {
      margin: 10px 0;
      font-size: 0.9rem;
      line-height: 1.2; }

